<template>
<div class="listItem">
    <div class="weui-form-preview">
    <div class="weui-form-preview__hd">
        <label class="weui-form-preview__label">订单状态</label>
        <em class="weui-form-preview__value">{{state}}</em>
        <label class="weui-form-preview__label">金额</label>
        <em class="weui-form-preview__value">¥{{price}}</em>
    </div>
    <div class="weui-form-preview__bd">
            <p>
            <label class="weui-form-preview__label">姓名</label>
            <span class="weui-form-preview__value">{{name}}</span>
        </p>
        <p>
            <label class="weui-form-preview__label">电话</label>
            <span class="weui-form-preview__value">{{phone}}</span>
        </p>
        <p>

            <label class="weui-form-preview__label">地址</label>
            <span class="weui-form-preview__value">{{address}}</span>
        </p>
         <p>

            <label class="weui-form-preview__label">快递信</label>
            <span class="weui-form-preview__value">{{expressSMS}}</span>
        </p>
        <p>
            <label class="weui-form-preview__label">备注</label>
            <span class="weui-form-preview__value">{{valiRemark}}</span>
        </p>
    </div>
    <div class="weui-form-preview__ft">
      <a class="weui-form-preview__btn weui-form-preview__btn_primary" :href="'tel:' + phone">拨打电话</a>
        <!--<a class="weui-form-preview__btn weui-form-preview__btn_primary" href="javascript:">确认送货</a>-->
    </div>
</div>
</div>
</template>
<script>
import userApi from 'components/mixins/api/user.vue'
export default {
  mixins: [userApi],
  name: 'sendItem',
  created: function () {
    let that = this
    this.getUser(this.userId, (err, data) => {
      if (err) {
        window.alert('请求错误')
      } else {
        that.name = data.name
        that.phone = data.phone
        that.position = JSON.parse(data.position)
      }
    })
  },
  data () {
    return {
      name: '',
      phone: '',
      position: ''
    }
  },
  computed: {
    valiRemark: function () {
      return this.remark === '' ? '无' : this.remark
    },
    state: function () {
      if (this.finish === '1') {
        return '已完成'
      } else if (this.finish === '2') {
        return '已取消'
      }
      return this.toker == null ? '待接单' : '已接单'
    },
    address: function () {
      let yuan = '1'
      if (this.position == null) {
        return '未填写'
      } else {
        let JSONData = typeof (this.postion) === 'string' ? JSON.parse(this.postion) : this.position
        switch (JSONData[1]) {
          case '1' :
            yuan = '北苑'
            break
          case '2' :
            yuan = '东苑'
            break
          case '3' :
            yuan = '西苑'
            break
          case '4' :
            yuan = '软件园'
            break
          default:
            yuan = '其他'
        }
        return yuan + '\t' + JSONData[2] + '号楼 ' + JSONData[3]
      }
    }
  },
  props: {
    userId: '',
    id: {
      require: true
    },
    toker: '',
    date: '',
    price: '',
    expressSMS: '',
    finish: '',
    remark: ''
  }
}
</script>
<style lang = "scss" scoped>
.listItem{
    font-size: 1.8rem;
    margin:9px;
}
</style>



