<script>
import $ from 'webpack-zepto'
export default{
  name: 'payApi',
  methods: {
    payOrder: function (fee, cb) {
      $.ajax({
        type: 'GET',
        url: '/api/pay.php?action=getPay&fee=' + fee * 100,
        success: function (data) {
          cb(null, data)
        },
        error: () => {
          cb('error', null)
        }
      })
    }
  }
}
</script>


