<template>
<div class="packageListItem" >
 <div class="weui-panel weui-panel_access">

    <div class="weui-panel__bd">
        <router-link :to="routerID"  class="weui-media-box weui-media-box_appmsg">
            <div class="weui-media-box__hd">
                <span class="weui-media-box__thumb" :class="genderClass"  >{{formatedPrice}}</span>
            </div>
            <div class="weui-media-box__bd">
                <h4 class="weui-media-box__title">{{address}}</h4>
                <p class="weui-media-box__desc">{{note}}</p>
                <ul class="weui-media-box__info">
                <li class="weui-media-box__info__meta">来源</li>
                <li class="weui-media-box__info__meta">时间</li>
                <li class="weui-media-box__info__meta weui-media-box__info__meta_extra">大小：{{valiSize}}</li>
            </ul>
            </div>
        </router-link>
    </div>
</div>
<i class="iconfont next">&#xe659;</i>
</div>
</template>
<script>
import util from 'components/mixins/util'
export default{
  mixins: [util],
  computed: {
    formatedPrice: function () {
      return this.formatPrice(this.price)
    },
    genderClass: function () {
      return this.gender === '1' ? 'packageListItemWomen' : 'packageListItemMan'
    },
    address: function () {
      let yuan = ''
      let JSONLocation = typeof (this.location) === 'string' ? JSON.parse(this.location) : this.location
      switch (JSONLocation[1]) {
        case '1' :
          yuan = '北苑'
          break
        case '2' :
          yuan = '东苑'
          break
        case '3' :
          yuan = '西苑'
          break
        case '4' :
          yuan = '软件园'
          break
        default:
          yuan = '未知'
      }
      return yuan + '\t' + JSONLocation[2] + '号楼'
    },
    valiSize: function () {
      switch (this.size) {
        case '1':
          return '小'
        case '2':
          return '中'
        case '3':
          return '大'
        default:
          return '未知'
      }
    },
    routerID: function () {
      return '/index/detail/' + this.id + '/' + this.address
    }
  },
  props: {
    id: '',
    price: {
      type: String,
      require: true
    },
    time: {
      type: String,
      default: '18:00--21:00'
    },
    location: {
      require: true
    },
    gender: String,
    size: String,
    note: {
      type: String,
      default: '暂无描述'
    }
  }
}
</script>
<style lang="scss" scoped>
@font-face {
  font-family: 'iconfont';  /* project id："170505" */
  src: url('//at.alicdn.com/t/font_p9vy3bkdvfx8yqfr.eot');
  src: url('//at.alicdn.com/t/font_p9vy3bkdvfx8yqfr.eot') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_p9vy3bkdvfx8yqfr.woff') format('woff'),
  url('//at.alicdn.com/t/font_p9vy3bkdvfx8yqfr.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_p9vy3bkdvfx8yqfr.svg#iconfont') format('svg');
}
.iconfont{
    font-family:"iconfont" !important;
    font-size:rem;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;}

 .packageListItemWomen{
     color:#FF9999;
 }
 .packageListItemMan{
     color:#89db89;
 }
 .packageListItem{
   position:relative;
 .next{
   position:absolute;
   z-index:9;
   top: 50%;
   color:gray;
   opacity:0.8;
  transform: translateY(-50%);
   right:0;

 }
 }
</style>



