<template>
  <div id="newOrder">
    <div class="weui-cells__title">表单</div>
<div class="weui-cells weui-cells_form">
    <div class="weui-cell weui-cell_vcode">
        <div class="weui-cell__hd">
            <label class="weui-label">价格</label>
        </div>
        <div class="weui-cell__bd">
            <input class="weui-input"  v-model="data.price" type="tel" readonly>
        </div>
        <div class="weui-cell__ft">
            <a href="javascript:;" class="weui-vcode-btn">RMB</a>
        </div>
    </div>
</div>
    <div class="weui-cells__title">物件大小</div>
<div class="weui-cells weui-cells_radio">
    <label class="weui-cell weui-check__label" for="x11">
        <div class="weui-cell__bd">
            <p>小</p>
        </div>
        <div class="weui-cell__ft">
            <input type="radio" class="weui-check" name="size" value="1" disabled v-model="data.size" id="x11">
            <span class="weui-icon-checked"></span>
        </div>
    </label>
    <label class="weui-cell weui-check__label" for="x12">

        <div class="weui-cell__bd">
            <p>中</p>
        </div>
        <div class="weui-cell__ft">
            <input type="radio" name="size" value="2" disabled class="weui-check"v-model="data.size" id="x12">
            <span class="weui-icon-checked"></span>
        </div>
    </label>
     <label class="weui-cell weui-check__label" for="x13">

        <div class="weui-cell__bd">
            <p>大</p>
        </div>
        <div class="weui-cell__ft">
            <input type="radio" name="size" value="3" disabled v-model="data.size" class="weui-check" id="x13">
            <span class="weui-icon-checked"></span>
        </div>
    </label>
</div>
    <div class="weui-cells__title">地址</div>
    <div class="weui-cells weui-cells_form">
    <div class="weui-cell">
        <div class="weui-cell__hd"><label class="weui-label">地址</label></div>
        <div class="weui-cell__bd">
            <input class="weui-input" type="text"  :value="$route.params.pos" readonly/>
        </div>
    </div>
</div>
<!--文本域-->
<div class="weui-cells__title">备注</div>
<div class="weui-cells weui-cells_form">
    <div class="weui-cell">
        <div class="weui-cell__bd">
            <textarea class="weui-textarea" readonly rows="2" v-model="data.remark"></textarea>
            <div class="weui-textarea-counter"><span></span></div>
        </div>
    </div>
</div>
<div class="button">
  <a href="javascript:;" @click=takeOrder() class="weui-btn weui-btn_primary">接单</a>
<router-link to="/index" href="javascript:;"  class="weui-btn weui-btn_warn">返回</router-link>
</div>
  </div>
</template>
<script>
  import orderAPI from 'components/mixins/api/order.vue'
export default {
    mixins: [orderAPI],
    name: 'detail',
    data () {
      return {
        data: ''
      }
    },
    created: function () {
      let id = this.$route.params.id
      this.get(id, (err, data) => {
        if (err) {
          window.alert('未知错误')
        } else {
          this.data = data
        }
      })
    },
    methods: {
      takeOrder: function () {
        let that = this
        this.take(this.$route.params.id, function (err, data) {
          if (err) {
            window.alert('未知错误！')
          } else {
            if (data.result === '成功') {
              window.alert('接单成功')
              that.$router.push('/state')
            } else {
              window.alert(data.reason)
            }
            that.$router.push('/index')
          }
        })
      }
    }
}
</script>
<style scoped lang="scss">
.button{
  margin:20px;
  margin-top:90px;
}
</style>



