<template>
<div>
  <div class="weui-cells__title weui-cell_vcode">修改电话</div>
  <div class="weui-cells">
    <div class="weui-cell">
      <div class="weui-cell__hd"><label class="weui-label">手机号</label></div>
      <div class="weui-cell__bd">
        <input class="weui-input" type="tel"  v-model="phone"  placeholder="请输入手机号">
      </div>
      <div class="weui-cell__ft">
            <a href="javascript:;"  @click="send()" v-show = "time === 0" class="weui-vcode-btn">获取验证码</a>
        </div>
    </div>
  </div>
  <div class="weui-cells__title">验证</div>
  <div class="weui-cells">
    <div class="weui-cell">
      <div class="weui-cell__hd">
        <label class="weui-label">验证码</label>
      </div>
      <div class="weui-cell__bd">
        <input type="tel" class="weui-input" v-model = "code" placeholder="请输入验证码">
      </div>
      <div class="weui_cell__ft">
         <a href="javascript:;" v-show="time !== 0" class="weui-vcode-btn">{{time}}s</a>
      </div>
    </div>
  </div>
  <div class="button">
    <a href="javascript:;" @click="check" class="weui-btn weui-btn_primary">确定</a>
  </div>
</div>
</template>
<script>
import SMSApi from 'components/mixins/api/SMS.vue'
import util from 'components/mixins/util.vue'
export default {
  name: 'hello',
  mixins: [SMSApi, util],
  data () {
    return {
      phone: '',
      id: '',
      time: 0,
      code: '',       // 验证码
      timerID: ''
    }
  },
  watch: {
    time: function (val) {
      if (val === 0) {
        clearInterval(this.timerID)
      }
    }
  },
  methods: {
    send: function () {
      let t = this.vali(this.phone, 'phone')
      if (!t) {
        window.alert('请输入正确的手机号！')
        return
      }
      // TODO 全局toast
      this.sendSMS(this.phone, (err, data) => {
        // data = JSON.parse(data)
        if (err) {
          window.alert('请求失败！')
        } else {
          this.id = data.reason
          this.startTimer()
          // window.alert(data.result)
        }
      })
    },
    check: function () {
      // let that = this
      this.checkSMS(this.id, this.code, (err, data) => {
        if (err || data == null) {
          window.alert('网络错误！')
        } else {
          data = typeof (data) === 'string' ? JSON.parse(data) : data
          if (data['result'] === '成功') {
            window.alert('修改成功')
            this.$router.push('/profile')
          } else if (data['result'] === '失败') {
            window.alert(data['result'])
          }
        }
      })
    },
    startTimer: function () {
      let that = this
      this.time = 60
      this.timerID = setInterval(() => {
        that.time--
      }, 1000)
    }
  }
}
</script>
<style>
  .button{
    margin: 20px;
    margin-top: 80px;
  }
</style>



