<template>
<div>
  <Loading
  :show="loadingFlag"></Loading>
  <div class="weui-cells__title">收货信息</div>
  <div class="weui-cells">
    <div class="weui-cell">
      <div class="weui-cell__hd"><label class="weui-label">收货姓名</label></div>
      <div class="weui-cell__bd">
        <input class="weui-input" type="text"  placeholder="请输入快递上的收货姓名"/>
      </div>
    </div>
    <div class="weui-cell">
      <div class="weui-cell__hd"><label class="weui-label">联系电话</label></div>
      <div class="weui-cell__bd">
        <input class="weui-input" type="text"  placeholder="联系电话"/>
      </div>
    </div>
  </div>
  <div class="weui-cells__title">地址信息</div>
  <div class="weui-cells">
    <div class="weui-cell weui-cell_select weui-cell_select-after">
      <div class="weui-cell__hd">
        <label for="" class="weui-label">所在苑</label>
      </div>
      <div class="weui-cell__bd">
        <select class="weui-select" name="direction">
          <option value="1">北苑</option>
          <option value="2">东苑</option>
          <option value="3">西苑</option>
          <option value="3">其他</option>
        </select>
      </div>
    </div>
    <div class="weui-cell">
      <div class="weui-cell__hd"><label class="weui-label">宿舍楼号</label></div>
      <input class="weui-input" type="text"  name="building" placeholder="请输入楼号">
    </div>
    <div class="weui-cell">
      <div class="weui-cell__hd"><label class="weui-label">寝室号</label></div>
      <div class="weui-cell__bd"><input class="weui-input" placeholder="请输入寝室号"></div>
    </div>
  </div>
  <div class="btn">
    <a href="javascript:;" class="weui-btn weui-btn_primary">提交</a>
    <a href="javascript:;" class="weui-btn weui-btn_default">放弃</a>
  </div>
</div>

</template>

<script>
import $ from 'webpack-zepto'
import Loading from 'components/loading'
export default {
  name: 'position',
  data () {
    return {
      loadingFlag: false
    }
  },
  methods: {
    getData: function () {
      this.loadingFlag = true
      let that = this
      $.ajax({
        type: 'GET',
        url: '/api/user.php',
        data: {action: 'getMine'},
        dataType: 'json',
        timeout: 100,
        success: function (data) {
          // {"Id":"4","sexual":"0","position":null,"phone":null,"head":"http:\/\/wx.qlogo.cn\/mmopen\/PiajxSqBRaEKCC0oBSpglTmZ9SDTK85wPCgCeHwO4ZUcgRMFUjOjXFmmMSOeBmQH6UdshHGcAjNHdiaJam4TloTQ\/0"}
          that.data = data
          that.loadingFlag = false
        },
        error: function () {
          that.loadingFlag = false
          that.showToast()
        }
      })
    }
  },
  created: function () {
    this.getData()
  },
  components: {
    Loading
  }
}
</script>


<style scoped lang="scss">
  .btn{
    margin: 20px;
    margin-top: 80px;
  }
</style>



