<script>
import $ from 'webpack-zepto'
export default{
  name: 'SMSApi',
  methods: {
    sendSMS: function (phone, cb) {
      $.ajax({
        dataType: 'json',
        type: 'post',
        url: '/api/SMS.php?action=get',
        data: {
          phone: phone
        },
        success: function (data) {
          cb(null, data)
        },
        error: () => {
          cb('error', null)
        }
      })
    },
    checkSMS: function (id, code, cb) {
      $.ajax({
        type: 'POST',
        url: '/api/SMS.php?action=check',
        data: {
          Id: id,
          code: code
        },
        success: data => {
          cb(null, data)
        },
        error: () => {
          cb('error', null)
        }
      })
    }
  }
}
</script>


