<template>
   <div>
      <div class="container">
        <div class="weui-tab">
    <div class="weui-navbar">
        <div class="weui-navbar__item" :class = "{'weui-bar__item_on':currentTab == 'own'}"  @click = "change('own')">
            我的
        </div>
        <div class="weui-navbar__item" :class = "{'weui-bar__item_on':currentTab == 'send'}"  @click = "change('send')">
            我接的
        </div>
    </div>
</div>
<div class="paddingTop">123</div>
  <List
  :currentList = "currentTab"
  :data = "data"
  ></List>
    </div>
   </div>
</template>
<script>
import List from './list.vue'
import orderApi from 'components/mixins/api/order.vue'
export default{
  mixins: [orderApi],
  components: {List},
  created: function () {
    this.getMine((err, data) => {
      if (err) {
        window.alert('网络错误！')
      } else {
        this.data = data
      }
    })
  },
  data () {
    return {
      data: '',
      currentTab: 'own'
    }
  },
  methods: {
    change: function (content) {
      this.currentTab = content
    }
  }
}
</script>
<style lang="scss" scoped>
.weui-bar__item_on{
  border-bottom: 2px solid #20b921;
}
</style>



