export default[{
  path: '/index',
  component: require('./views/packages/index')
}, {
  path: '/index/detail/:id/:pos',
  component: require('./views/packages/details')
}, {
  path: '/profile',
  component: require('./views/profile/index')
}, {
  path: '/profile/phone',
  component: require('./views/profile/phone')
}, {
  path: '/profile/position',
  component: require('./views/profile/position')
}, {
  path: '/state',
  component: require('./views/state/index')
}, {
  path: '/index/order',
  component: require('./views/packages/order')
}
]



