<script>
import $ from 'webpack-zepto'
export default {
  name: 'util',
  methods: {
    // 只传一个data，会迭代出里面所有的内容对未定义和‘’做判断，并弹出键值名提醒
    vali: function (data, type) {
      let result = true
      if (type !== undefined) {
        switch (type) {
          case 'phone':
            return /^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/.test(data)
          case 'price':
            if (!/\d/.test(data)) {
              window.alert('快递的价格必须是数字呦~')
              return false
            } else {
              let value = parseInt(data)
              if (value >= 1 && value <= 30) {
                return true
              } else {
                window.alert('请输入1元到30元之间的价格！')
                return false
              }
            }
        }
      } else {
        $.each(data, (key, value) => {
          if ((typeof (value) === 'string' && value === '') || value === undefined) {
            window.alert(key + '不能为空！')
            result = false
          }
        })
        return result
      }
    },
    formatPrice: function (price) {
      if (/^\d{1}$/.test(price)) {
        return price + '.0'
      } else {
        return price
      }
    }
  }
}
</script>



