<template>
<div class="container">
  <div id="toast" style="display: none;">
    <div class="weui-mask_transparent"></div>
    <div class="weui-toast">
      <i class="weui-icon-success-no-circle weui-icon_toast"></i>
      <p class="weui-toast__content">已完成</p>
    </div>
  </div>
<div class="weui-cells__title"></div>
<div class="weui-cells">
    <a class="weui-cell weui-cell_access" href="javascript:;">
        <div class="weui-cell__bd">
            <p>头像</p>
        </div>
        <div class="weui-cell__ft">
          <img class="weui_avatar" :src="headURL" alt="">
        </div>
    </a>
    <a class="weui-cell weui-cell_access" href="javascript:;">
        <div class="weui-cell__bd">
            <p>昵称</p>
        </div>
        <div class="weui-cell__ft">
        {{data.name == null ? "暂无" : data.name}}
        </div>
    </a>
     <a class="weui-cell weui-cell_access" @click="showDialog('性别')">
        <div class="weui-cell__bd">
            <p>性别</p>
        </div>
        <div class="weui-cell__ft">
          {{data.sexual == "1" ? "女" : "男"}}
        </div>
    </a>

</div>
  <div class="weui-cells__title">收货信息</div>
  <div  @click="active()" class="weui-cells">
<router-link to="/profile/phone" class="weui-cell weui-cell_access">
        <div class="weui-cell__bd">
            <p>联系电话</p>
        </div>
        <div class="weui-cell__ft">
          {{data.phone == null ? '暂无' : data.phone}}
        </div>
    </router-link>
    <div class="weui-cell">
      <div class="weui-cell__hd"><label class="weui-label">快递姓名</label></div>
      <div class="weui-cell__bd">
        <input class="weui-input" type="text"  v-model = "pos[0]" placeholder="请输入快递上的收货姓名"/>
      </div>
    </div>
  </div>
  <div class="weui-cells__title">地址信息</div>
  <div class="weui-cells" @click="active()">
    <div class="weui-cell weui-cell_select weui-cell_select-after">
      <div class="weui-cell__hd">
        <label for="" class="weui-label">所在苑</label>
      </div>
      <div class="weui-cell__bd" >
        <select class="weui-select"   v-model = "pos[1]" name="direction">
          <option value="1">北苑</option>
          <option value="2">东苑</option>
          <option value="3">西苑</option>
          <option value="4">软件园</option>
        </select>
      </div>
    </div>
    <div class="weui-cell">
      <div class="weui-cell__hd"><label class="weui-label">宿舍楼号</label></div>
      <div class="weui-cell__bd">
        <input class="weui-input" type="text"  v-model = "pos[2]" name="building" placeholder="请输入楼号">
      </div>
    </div>
    <div class="weui-cell">
      <div class="weui-cell__hd"><label class="weui-label">寝室号</label></div>
      <div class="weui-cell__bd"><input class="weui-input" v-model = "pos[3]" placeholder="请输入寝室号"></div>
    </div>
  </div>
  <div class="btn">
    <a  class="weui-btn weui-btn_primary" @click="editData" v-bind:class="{ 'weui-btn_disabled': !activeFlag}" >提交</a>
  </div>


  <!--弹出层-->
  <div v-show="dialogFlag">
    <div class="weui-mask"></div>
    <div class="weui-dialog">
      <div class="weui-dialog__hd">性别</div>
      <div class="weui-dialog__bd">
        <div class="weui-cells">

          <!--gender-->
          <div >
            <label class="weui-cell">
              <div class="weui-cell__bd">
                <p>男</p>
              </div>
              <div class="weui-cell__ft">
                <input type="radio" name="gender" value="0" v-model="data.sexual">
              </div>
            </label>
            <label class="weui-cell">
              <div class="weui-cell__bd">
                <p>女</p>
              </div>
              <div class="weui-cell__ft">
                <input type="radio" name="gender" value="1" v-model = "data.sexual">
              </div>
            </label>
          </div>
          <!--gender-->

        </div>
      </div>
      <div class="weui-dialog__ft">
        <a  @click="submitDialog" class="weui-dialog__btn weui-dialog__btn_primary">确定</a>
      </div>
    </div>
  </div>
  <Loading
    :show = "loadingFlag"></Loading>
  <Toast
  :show="toastFlag"
  :type="toastType">
  </Toast>
</div>
</template>
<script>
import util from 'components/mixins/util.vue'
import $ from 'webpack-zepto'
import Loading from 'components/loading'
import Toast from 'components/toast'
export default {
  name: 'profile',
  mixins: [util],
  data: () => {
    return {
      activeFlag: false,       // 用户是否修改了信息: false,       // 用户是否修改了信息
      pos: ['', '', '', ''],
      toastType: '',
      toastFlag: false,
      dialogFlag: false,
      address: '',
      data: '',
      loadingFlag: false,
      genderRadio: ''
    }
  },
  computed: {
    headURL: function () {
      // TODO 转义，前段还是后端
      return this.data.head
    }
  },
  components: {
    Loading, Toast
  },
  created: function () {
    this.loadingFlag = true
    this.getData()
  },
  methods: {
    active: function () {
      this.activeFlag = true
    },
    editData: function () {
      let that = this
      let ajaxData = {
        Id: that.data.Id,
        name: that.data.name,
        // phone: that.data.phone,
        position: JSON.stringify(that.pos),
        sexual: that.data.sexual
      }
      let t = this.vali(ajaxData)
      let tt = this.vali({
        '收货姓名': that.pos[0],
        '宿舍楼号': that.pos[2],
        '寝室号': that.pos[3]
      })
      if (!t || !tt || !this.activeFlag) {
        return
      }
      this.loadingFlag = true
      $.ajax({
        type: 'POST',
        url: '/api/user.php?action=edit',
        dataType: 'json',
        data: ajaxData,
        success: function (data) {
          that.loadingFlag = false
          if (data['reason'] == null) {
            that.showToast('success')
          }
        },
        error: function () {
          that.loadingFlag = false
          that.showToast('error')
        }
      })
    },
    showToast: function (type = 'error') {
      this.toastFlag = true
      this.toastType = type
      let that = this
      setTimeout(() => {
        that.toastFlag = false
      }, 2000)
    },
    showDialog: function () {
      this.active()
      this.sex = this.data.sexual
      this.dialogFlag = true
    },
    submitDialog: function () {
      this.dialogFlag = false
    },
    getData: function () {
      let that = this
      $.ajax({
        type: 'GET',
        url: '/api/user.php?action=getMine',
        dataType: 'json',
        success: function (data) {
          // {"Id":"4","sexual":"0","position":null,"phone":null,"head":"http:\/\/wx.qlogo.cn\/mmopen\/PiajxSqBRaEKCC0oBSpglTmZ9SDTK85wPCgCeHwO4ZUcgRMFUjOjXFmmMSOeBmQH6UdshHGcAjNHdiaJam4TloTQ\/0"}
          that.data = data
          if (that.data.position != null) {
            that.pos = JSON.parse(that.data.position)
          }
          that.loadingFlag = false
        },
        error: function () {
          that.loadingFlag = false
          that.showToast('error')
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
  .weui_avatar{
    width:52px;
    height:52px;
    border-radius:6px;
  }
  .btn{
    margin: 20px;
    margin-top: 35px;
  }
  .weui-input{
    text-align:right;
    &:focus{
      text-align:left;
    }
  }
  .weui-select{
    direction:rtl;
    &:focus{
      direction:ltr;
    }
  }
</style>



