<template>
  <div id="toast" v-show="show">
  <div class="weui-mask_transparent"></div>
  <div class="weui-toast">
  <i class="weui-icon_toast" :class = "iconType"></i>
    <br>
  <p class="weui-toast__content"><slot>{{msg}}</slot></p>
  </div>
  </div>
</template>
<style lang="scss">
#toast{
  font-size:1.2rem;
}
</style>
<script>
export default{
// TODO 这个怎么设计才能把逻辑都写到这个组件里呢？
  props: {
    show: {
      type: Boolean,
      required: true
    },
    timeout: {
    // error
      type: Number,
      default: 2000
    },
    type: {
      type: String
    }
  },
  computed: {
    iconType: function () {
      return this.type === 'success' ? 'weui-icon-success-no-circle' : 'weui-icon-warn'
    },
    msg: function () {
      return this.type === 'success' ? '成功' : '失败'
    }
  },
  watch: {
    errorshow: function (val) {
      if (val) {
        let that = this
        setTimeout(() => {
          that.show = false
        }, that.timeout)
      }
    }
  }
}
</script>



