<script>
import $ from 'webpack-zepto'
export default{
  name: 'orderApi',
  methods: {
    getAll: function (page, cb) {
      $.ajax({
        type: 'POST',
        dataType: 'json',
        url: '/api/order.php?action=getAll',
        data: {
          page: page
        },
        success: function (data) {
          cb(null, data)
        },
        error: () => {
          cb('error', null)
        }
      })
    },
    getMine: function (cb) {
      $.ajax({
        type: 'POST',
        dataType: 'json',
        url: '/api/order.php?action=getMine',
        success: function (data) {
          cb(null, data)
        },
        error: () => {
          cb('error', null)
        }
      })
    },
    take: function (id, cb) {
      $.ajax({
        type: 'POST',
        url: '/api/order.php?action=take',
        data: {
          Id: id
        },
        dataType: 'json',
        success: function (data) {
          cb(null, data)
        },
        error: () => {
          cb('error', null)
        }
      })
    },
    get: function (id, cb) {
      $.ajax({
        type: 'POST',
        dataType: 'json',
        data: {
          Id: id
        },
        url: '/api/order.php?action=get',
        success: function (data) {
          cb(null, data)
        },
        error: () => {
          cb('error', null)
        }
      })
    },
    cancel: function (id, cb) {
      $.ajax({
        type: 'POST',
        dataType: 'json',
        url: '/api/order.php?action=cancel',
        data: {
          Id: id
        },
        success: function (data) {
          cb(null, data)
        },
        error: function () {
          cb('err', null)
        }
      })
    },
    finishApi: function (id, cb) {
      $.ajax({
        type: 'POST',
        dataType: 'json',
        url: '/api/order.php?action=finish',
        data: {
          Id: id
        },
        success: function (data) {
          cb(null, data)
        },
        error: function () {
          cb('err', null)
        }
      })
    }
  }
}
</script>


