<template>
<div class="listItem">
    <div class="weui-form-preview">
    <div class="weui-form-preview__hd">
        <label class="weui-form-preview__label">订单状态</label>
        <em class="weui-form-preview__value">{{state}}</em>
        <label class="weui-form-preview__label">金额</label>
        <em class="weui-form-preview__value">¥{{price}}</em>
    </div>
    <div class="weui-form-preview__bd">
         <div class="toker" v-if="toker != null">
        <p>
            <label class="weui-form-preview__label">接单人姓名</label>
            <span class="weui-form-preview__value">{{name}}</span>
        </p>
        <p>
            <label class="weui-form-preview__label">电话</label>
            <span class="weui-form-preview__value">{{phone}}</span>
        </p>
         </div>
         <p>
            <label class="weui-form-preview__label">快递信</label>
            <span class="weui-form-preview__value">{{expressSMS}}</span>
        </p>
          <p>
            <label class="weui-form-preview__label">备注</label>
            <span class="weui-form-preview__value">{{valiRemark}}</span>
        </p>
    </div>
    <div v-show = "showFlag" class="weui-form-preview__ft">
      <a v-show= "toker == null" @click="cancelOrder()" class="weui-form-preview__btn weui-form-preview__btn_primary" >取消订单</a>
      <a :href="'tel:' + phone" v-show="toker != null" class="weui-form-preview__btn weui-form-preview__btn_primary" >拨打电话</a>
        <a   v-show="toker != null" @click="confirmOrder()" class="weui-form-preview__btn weui-form-preview__btn_primary" href="javascript:">确认收货</a>
    </div>
</div>
</div>
</template>
<script>
import userApi from 'components/mixins/api/user.vue'
import orderApi from 'components/mixins/api/order.vue'
export default {
  mixins: [userApi, orderApi],
  name: 'ownlistItem',
  methods: {
    cancelOrder: function () {
      this.cancel(this.id, (err, data) => {
        if (err || data == null) {
          window.alert('网络错误，请重试！')
        } else {
          let jsonData = typeof (data) === 'string' ? JSON.parse(data) : data
          window.alert(jsonData.reason)
        }
      })
    },
    confirmOrder: function (id) {
      let flag = window.confirm('是否确认收货？')
      if (!flag) {
        return
      }
      this.finishApi(this.id, (err, data) => {
        if (err || data == null) {
          window.alert('网络错误，请重试！')
        } else {
          let jsonData = typeof (data) === 'string' ? JSON.parse(data) : data
          window.alert(jsonData.result)
          window.location.reload()
        }
      })
    }
  },
  computed: {
    valiRemark: function () {
      return this.remark === '' ? '无' : this.remark
    },
    showFlag: function () {
      return this.finish === '0'
    },
    state: function () {
      if (this.finish === '1') {
        return '已完成'
      } else if (this.finish === '2') {
        return '已取消'
      }
      return this.toker == null ? '待接单' : '已接单'
    }
  },
  data () {
    return {
      name: '',
      phone: ''
    }
  },
  props: {
    id: {
      require: true
    },
    toker: '',
    date: '',
    price: '',
    expressSMS: '',
    finish: '',
    remark: ''
  },
  mounted: function () {
    if (this.toker != null) {
      let that = this
      this.getUser(this.toker, (err, data) => {
        if (err) {
          window.alert('请求错误')
        } else {
          that.name = data.name
          that.phone = data.phone
        }
      })
    }
  }
}
</script>
<style lang = "scss" scoped>
.listItem{
    font-size: 1.8rem;
    margin:9px;

}
</style>


