import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import routerConfig from './routerConfig'

Vue.use(VueRouter)
const router = new VueRouter({routes: routerConfig})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router: router,
  template: '<App/>',
  // render: v => v(App)
  components: { App }
})



