<script>
export default {
  name: 'toastMixin',
  data () {
    return {
      toast: {
        flag: false,
        type: 'error',
        text: ''
      },
      loading: {
        flag: false
      }
    }
  },
  methods: {
    showToast: function (type, text, delay = 500) {
      let that = this
      this.toast.text = text
      this.toast.flag = true
      setTimeout(() => {
        that.toast.flag = false
      }, delay)
    }
  }
}
</script>



