<template>
<div>
  <ownListItem v-if="tab === '我发布的'" v-for="item in data[tab]"
  :id="item.Id"
  :toker="item.toker"
  :date="item.date"
  :price="item.price"
  :expressSMS="item.expressSMS"
  :finish="item.finish"
  :remark="item.remark"
  ></ownListItem>
  <sendListItem v-if="tab === '我接的'" v-for="item in data[tab]"
  :userId = "item.userId"
  :id="item.Id"
  :toker="item.toker"
  :date="item.date"
  :price="item.price"
  :expressSMS="item.expressSMS"
  :finish="item.finish"
  :remark= "item.remark"
  ></sendListItem>
</div>
</template>
<script>
import ownListItem from './ownListItem.vue'
import sendListItem from './sendListItem.vue'
export default {
  props: {
    data: '',
    currentList: ''
  },
  computed: {
    tab: function () {
      switch (this.currentList) {
        case 'own' :
          return '我发布的'
        case 'send' :
          return '我接的'
        default:
          return '我发布的'
      }
    }
  },
  name: 'List',
  components: {
    ownListItem,
    sendListItem
  }
}
</script>
<style>

</style>


