<script>
import $ from 'webpack-zepto'
export default{
  name: 'userAPI',
  methods: {
    getUser: function (id, cb) {
      $.ajax({
        dataType: 'json',
        type: 'post',
        url: '/api/user.php?action=getUser',
        data: {
          Id: id
        },
        success: function (data) {
          cb(null, data)
        },
        error: () => {
          cb('error', null)
        }
      })
    },
    getMine: function (cb) {
      $.ajax({
        type: 'GET',
        url: '/api/user.php?action=getMine',
        dataType: 'json',
        success: function (data) {
          cb(null, data)
        },
        error: function () {
          cb('err', null)
        }
      })
    }
  }
}
</script>


