<template>
  <div class="loadingContainer">
    <div id="loadingToast" v-show="show">
      <div class="weui-mask_transparent"></div>
      <div class="weui-toast">
        <i class="weui-loading weui-icon_toast"></i>
        <p class="weui-toast__content">{{msg}}</p>
      </div>
    </div>
  </div>
</template>
<style lang="scss">
.loadingContainer{
  font-size: 1.6rem;
}
</style>
<script>
    export default{
      name: 'loading',
      props: {
        show: false,
        msg: {
          type: String,
          default: '数据加载中'
        }

      }
    }
</script>



